import React from "react";
import { Button, Input, InputGroup, Select, Text } from "sancho";
import { FormWrapper, BtnWrap } from "./style";

interface AccountsItemProps {
  name: string;
  currency: string;
  balance: number;
}

interface AddFormProps {
  addListItem: (AccountsItemProps) => void;
}

export class AddForm extends React.Component<AddFormProps, {}> {
  submitHandler = (ev: React.FormEvent) => {
    ev.preventDefault();
    this.props.addListItem("");
  };

  render() {
    return (
      <FormWrapper onSubmit={this.submitHandler}>
        <Text variant="h3"> </Text>
        <InputGroup label="">
          <Input
            required
            inputSize="lg"
            placeholder=""
            type="text"
            name="nameListItem"
            value=""
          />
        </InputGroup>
        <InputGroup label=" ">
          <Select inputSize="lg">
            <option value="cash"></option>
            <option value="cards"> </option>
            <option value="deposits"></option>
          </Select>
        </InputGroup>
        <InputGroup label="">
          <Select inputSize="lg">
            <option value=""></option>
            <option value=""></option>
            <option value="$"> $</option>
          </Select>
        </InputGroup>
        <InputGroup label=" ">
          <Input
            inputSize="lg"
            placeholder=" "
            type="text"
            name="balanceListItem"
            value=""
          />
        </InputGroup>
        <BtnWrap>
          <Button intent="primary" type="submit" block component="button">
            
          </Button>
        </BtnWrap>
      </FormWrapper>
    );
  }
}
