import React from "react";
import { Button, Input, InputGroup, Select, Text, TextArea } from "sancho";
import { FormWrapper, BtnWrap } from "./style";

interface TransactionsItemProps {
  amount: number;
  category: string;
  account: string;
  isIncome: boolean;
  isOutgo: boolean;
  comment: string;
}
interface AddFormProps {
  addListItem: (TransactionsItemProps) => void;
}
export class AddForm extends React.Component<AddFormProps, {}> {
  submitHandler = (ev: React.FormEvent) => {
    ev.preventDefault();
    this.props.addListItem("");
  };
  render() {
    return (
      <FormWrapper onSubmit={this.submitHandler}>
        <Text variant="h3"> </Text>
        <InputGroup label=" ">
          <Select inputSize="md">
            <option value="income"></option>
            <option value="outgo"></option>
          </Select>
        </InputGroup>
        <InputGroup label="">
          <Input
            required
            inputSize="md"
            placeholder=""
            type="text"
            name="amount"
            value=""
          />
        </InputGroup>
        <InputGroup label="">
          <Select inputSize="md">
            <option value="cash"></option>
            <option value="cards"></option>
            <option value="cards">/</option>
            <option value="cards"></option>
            <option value="cards">Freelance</option>
          </Select>
        </InputGroup>
        <InputGroup label="">
          <Select inputSize="md">
            <option value=""></option>
            <option value=" "> </option>
            <option value=" "> </option>
            <option value=" "> </option>
            <option value=" "> </option>
          </Select>
        </InputGroup>
        <InputGroup label="">
          <TextArea placeholder="   " />
        </InputGroup>
        <BtnWrap>
          <Button intent="primary" type="submit" block component="button">
            
          </Button>
        </BtnWrap>
      </FormWrapper>
    );
  }
}
